.page {
  font-family: "Poppins", sans-serif;
  color: #FFFFFF;
  font-size: 16px;
  line-height: 22.4px;

  scroll-behavior: smooth;
}

.page__body {
  margin: 0;
}

.page__menu {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;

  opacity: 0;
  pointer-events: none;
}
.page__menu:target {
  opacity: 1;
  pointer-events: all;
}

.page:has(.page__menu:target) {
  overflow: hidden; /* not to scroll the page */
}

.header {
  box-sizing: border-box;
  height: 100vh;
  background-image: url('../images/Cover_photo.png');
  background-size: cover;
  background-position: center;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.top-bar {
  padding: 120px 36px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.top-bar__icons {
  display: flex;
  gap: 32px;
}

.top-bar__logo {
  display: flex;
}

.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: #C2C2C2;
  font-weight: 700;
  text-transform: uppercase;
  color: #1D1D1D;

  overflow: auto; /* to scroll the menu if needed */
}

.menu__top {

  position: sticky; /* to keep cross always visible */
  top: 0;
  z-index: 1;
  background-color: #C2C2C2;
}

.header__title {
  margin: 0;
  font-weight: 700;
  font-size: 80px;
  line-height: 80px;
  letter-spacing: -1.5;
  text-align: center;

  padding-bottom: 120px;
}

.header__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__bottom {
  padding: 0 36px 72px;
}

.menu__nav {
  margin-bottom: 40px;
}

.menu__content {
  padding-inline: 36px;
}

.menu__phone-number {
  display: block;
  margin-bottom: 16px;
  color: inherit;
  text-decoration: none;
  font-size: 16px;
  line-height: 27px;
}

.menu__book-a-test-ride {
  display: block;
  width: fit-content;
  color: inherit;
  text-decoration: none;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 2px;
  padding-bottom: 7px;
  border-bottom: 1px solid #1D1D1D;
}

.nav__link {
  color: inherit;
  text-decoration: none;
  font-size: 22px;
  line-height: 22px;
  letter-spacing: 2px;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 32px;
}


.icon {
  height: 32px;
  width: 32px;
  display: block;
}

.icon--phone {
  background-image: url('../images/Icon-Phone-call.png');
}

.icon--menu {
  background-image: url('../images/Icon-Burger-menu.png');
}

.icon--close {
  background-image: url('../images/Icon-Close.png');
}

.main {
  background-color: #1D1D1D;
}

.move-free {
  display: flex;
  justify-content: space-evenly;
  align-items: flex-end;
}

.move-free__title {
  font-weight: 500;
  font-size: 64px;
  line-height: 64px;
  letter-spacing: -1.5px;
}

.move-free__content {
  font-weight: 500;
  font-size: 23px;
  line-height: 32.2px;
}

.compare-bikes {
  display: flex;
}

.compare-bikes__title {
  font-weight: 500;
  font-size: 64px;
  line-height: 64px;
  letter-spacing: -1.5px;
}

.img__bicycle {
  height: 256px;
  width: 324px;
  display: block;
}

.img__sporty-4 {
  background-image: url('../images/bicycle1.png');
}

.img__ride-in-town-st {
  background-image: url('../images/bicycle2.png');
}

.img__agile-ride-3 {
  background-image: url('../images/bicycle3.png');
}
